"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";
import DotPattern from "./dot-pattern";
import { Check } from "lucide-react";
import * as React from "react";
import { useGSAP, prefersReducedMotion } from "@/lib/gsap-utils";

export default function CtaSection() {
  const ref = React.useRef<HTMLDivElement | null>(null);

  useGSAP(() => {
    if (prefersReducedMotion()) return;
    const root = ref.current;
    if (!root) return;
    import("gsap/dist/gsap").then(({ gsap }) => {
      import("gsap/dist/ScrollTrigger").then(({ ScrollTrigger }) => {
        gsap.registerPlugin(ScrollTrigger);
        gsap.fromTo(
          root.querySelectorAll("[data-cta-anim]"),
          { opacity: 0, y: 16 },
          { opacity: 1, y: 0, duration: 0.6, ease: "power2.out", stagger: 0.15, scrollTrigger: { trigger: root, start: "top 80%" } }
        );
      });
    });
  });

  return (
    <section id="cta" className="relative w-full py-24 md:py-32 overflow-hidden">
      <DotPattern className="opacity-20" />
      <div className="container px-4 md:px-6" ref={ref}>
        <div className="relative overflow-hidden rounded-2xl p-12 md:p-16 text-center" style={{ backgroundImage: 'var(--gradient-cta)' }}>
            <div className="inline-flex items-center gap-2 rounded-full glass-dark px-3 py-1" data-cta-anim>
              <span></span>
              <span className="text-sm font-medium">Start Your Journey</span>
            </div>
            <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold text-white drop-shadow" data-cta-anim>
              Ready to Begin Your Web Development Voyage?
            </h2>
            <p className="mx-auto max-w-[700px] text-white/90 md:text-xl" data-cta-anim>
              Take the first step towards mastering HTML. Create your free account and start learning with the clearest notes on the web.
            </p>
            <div className="mx-auto grid max-w-3xl grid-cols-1 gap-3 sm:grid-cols-3" data-cta-anim>
              {['500+ Curated Notes', 'AI-Powered Editing', 'Free Forever'].map((f) => (
                <div key={f} className="glass-dark rounded-full px-3 py-2 text-white/90 text-sm flex items-center justify-center gap-2">
                  <Check className="h-4 w-4 text-white" />
                  <span>{f}</span>
                </div>
              ))}
            </div>
            <Button asChild size="lg" className="shadow-glow-purple hover:scale-110 transition" data-cta-anim>
              <Link href="/signup">Access Notes Now </Link>
            </Button>
          </div>
        </div>
    </section>
  );
}
